@font-face {
  font-family: 'source_code_proregular';
  src: url('./fonts/sourcecodepro-regular-webfont.woff2') format('woff2'),
    url('./fonts/sourcecodepro-regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  height: 100%;
}

svg {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  height: 100px;
  max-width: 100%;
  width: auto;
  pointer-events: none;
  outline: 1px solid#000;
  margin: auto;
}

.is-blackBg svg {
  outline: 1px solid #fff;
}

.EnhanceButton,
.ColorToggleButton {
  -webkit-appearance: none;
  appearance: none;
  padding: 0;
  border: 0;
  font: inherit;
  display: block;
  background-color: transparent;
  margin: 0;
  margin-bottom: 1rem;
  padding: .25em;
}
.EnhanceButton:focus,
.ColorToggleButton:focus {
  outline: 0;
}

.EnhanceButton {
  cursor: zoom-in;
  box-shadow: rgb(99 99 99 / 20%) 0px 2px 8px 0px;
  margin: .25em;
  display: block;
  transition: background-color .25s, outline .25s, border-color .25s;
  background-color: #fff;
  border-radius: 8px;
  border: 3px solid transparent;
  min-width: 100px;
}
.EnhanceButton:hover,
.EnhanceButton:focus {
  border-color: #7298a0;
}

.PreviewSection {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.PreviewSection:not(:empty) {
  margin-bottom: 1em;
}
.is-showingSvg svg {
  position: static;
  opacity: 1;
}

.ColorToggleWrap {
  display: flex;
  justify-content: center;
  flex-basis: 100%;
}

.ColorToggleButton {
  position: relative;
  font-size: 1rem;
  width: 20px;
  height: 20px;
  border: 1px solid #fff;
  border-radius: 4px;
  background-color: #000;
  cursor: pointer;
  transition: background-color .25s, border-color .25s;
  margin-top: 1em;
}
.ColorToggleButton::after {
  content: '';
  position: absolute;
  top: -6px;
  left: -6px;
  width: 20px;
  height: 20px;
  border: 1px solid #000;
  border-radius: 4px;
  background-color: #fff;
  transition: background-color .25s, border-color .25s;
}

.AccessibleText {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal !important;
}

.is-blackBg .ColorToggleButton {
  background-color: #fff;
  border-color: #000;
}
.is-blackBg .ColorToggleButton::after {
  background-color: #000;
  border-color: #fff;
}


.is-enhanced svg {
  pointer-events: none;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}

.EnhanceButton.is-enhanced {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: calc(100% - 2em);
  height: calc(100% - 2em);
  padding: 1em;
  z-index: 99999;
  cursor: zoom-out;
  margin: 1em;
  border: 0;
  background: white;
  box-shadow: rgb(0 0 0 / 7%) 0px 1px 2px, rgb(0 0 0 / 7%) 0px 2px 4px, rgb(0 0 0 / 7%) 0px 4px 8px, rgb(0 0 0 / 7%) 0px 8px 16px, rgb(0 0 0 / 7%) 0px 16px 32px, rgb(0 0 0 / 7%) 0px 32px 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.is-blackBg .EnhanceButton {
  background-color: #000;
}

