a:focus {
  outline: 1px dotted white;
  outline-offset: 2px;
}
.Logo-wrap {
  width: 80%;
  max-width: 400px;
  min-width: 280px;
}

.Header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #2d2a2a;
  padding: 2em;
  color: #fff;
  position: relative;
  width: 100%;
}
.Header-title {
  font-size: 1rem;
  line-height: 1.3;
  margin: 1em 0 0;
  text-align: center;
}

@media (min-width: 800px) {
  .Header-title {
    font-size: 1.5em;
  }
}

main {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  background-color: #f3f3f3;
  border-radius: 8px;
  border: 6px solid #7298a0;
  padding: 1.5em;
  margin: 2em;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  transition: background-color 0.25s, border-color 0.25s;
  max-width: 1200px;
  width: calc(100% - 4em);
  flex-grow: 1;
  font-size: 1.5em;
}

@media (min-width: 800px) {
  main {
    font-size: 2em;
  }
}
main.is-hovered {
  background-color: rgba(255, 255, 255, 0.6);
  border-color: #a43365;
}
.is-hovered * {
  display: none;
}
.DropText {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  pointer-events: none;
}
.is-hovered .DropText {
  display: block;
}

input[type='file'] {
  display: none;
}
label {
  -webkit-appearance: none;
  appearance: none;
  font: inherit;
  background: inherit;
  padding: 0;
  border: 0;
  color: #a43365;
  font-weight: bold;
  cursor: pointer;
}
label:hover,
label:focus {
  text-decoration: underline;
}

.CopyInput {
  position: absolute;
  transform: translateX(-9999px);
}

.MainContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.ButtonWrap:not(:empty) {
  margin-top: 1em;
}

.DownloadButton,
a[download],
.CopyButton {
  display: inline-block;
  font-size: 1em;
  margin: 0 0.5em;
  margin-top: 1em;
  transition: background-color 0.25s;
  cursor: pointer;
  font: inherit;
  border: 0;
  appearance: none;
}
.DownloadButton,
a[download] {
  color: inherit;
  text-decoration: none;
  background-color: #a43365;
  border-radius: 8px;
  color: #fff;
  padding: 0 0.5em;
}
.DownloadButton:hover,
.DownloadButton:focus,
a[download]:hover,
a[download]:focus {
  background-color: #cc4f60;
  outline: 0;
}
.CopyButton {
  -webkit-appearance: none;
  appearance: none;
  border: 0;
  border-radius: 8px;
  color: #fff;
  padding: 0 0.5em;
  background: #7298a0;
}
.CopyButton:hover,
.CopyButton:focus {
  background-color: #66b0ae;
}

.Footer {
  width: 100%;
  background-color: #2d2a2a;
  text-align: center;
  color: #fff;
  padding: 0 2em;
  font-size: .85em;
}
.Footer-link {
  cursor: pointer;
  color: #7298a0;
  font-weight: bold;
  text-decoration: none;
}
.Footer-link:hover,
.Footer-link:focus {
  text-decoration: underline;
}